import '../../assets/css/iconfont.css'
import router from './router'
import Vue from 'vue'
import ElementUI from 'element-ui'
import UserView from './UserView.vue'
import _Fun from '../../utils/Fun'
import _EL from '../../utils/EL'
import _service2 from '../../config/service2'

Vue.prototype.Fun = _Fun
Vue.prototype.EL = _EL
Vue.prototype.baseURL = BASE_URL
Vue.prototype.SERVICE2 = _service2
Vue.use(ElementUI,{ size: 'small' });
Vue.config.productionTip = false;

/* eslint-disable no-new */
new Vue({
  el: '#account',
  router,
  template: '<UserView/>',
  components: {UserView}
})



