<template>
  <div style="margin:auto;width:380px;margin-top:70px">
    <el-card :body-style="{ padding: '15px'}" style="border-radius:15px">
      <div slot="header" class="clearfix">
        <span style="color:#E6A23C;font-weight:bold;font-size:18px;line-height:1.6">小白问卷登录</span>
        <el-button onclick="javascript:location.href='/'" style="float: right;" type="success" round>首页</el-button>
      </div>
      <div class="thumbnail">
        <img src="resource/img/logo.png" class="img-thumbnail" style="border: none">
      </div>
      <div style="padding: 14px;">
        <div>
          <!--span style="font-size:14px;color:#9c9c9c">用户名</span-->
          <el-input v-model="user.userName"
                    placeholder="请输入登录账号"
                    prefix-icon="el-icon-phone-outline">
          </el-input>
        </div>
        <div style="margin-top:10px">
          <!--span style="font-size:14px;color:#9c9c9c">密码</span-->
          <el-input type="password" v-model="user.passwd"
                    placeholder="请输入登录密码"
                    prefix-icon="el-icon-goods">
          </el-input>
        </div>
        <div v-if="false" style="margin-top:10px;display: flex">
          <div>
            <el-input size="medium" v-model="user.captcha" placeholder="请输入验证码"
                      prefix-icon="el-icon-location-outline"/>
          </div>
          <span>&nbsp;</span>
          <img :src="imgUrl" style="height:36px;width:75px"/>
          <span>&nbsp;</span>
          <el-button @click="refresh">刷新</el-button>
        </div>
        <div style="margin-top:20px">
          <el-button @click="login" round type="primary" style="width:100%;font-size:18px">登 录</el-button>
        </div>
        <div style="margin-top:15px;text-align: center">
            <div>
            <el-button round type="text" @click="register" style="font-size:14px">立即注册</el-button>
            <a v-if="Fun.getDevice()=='pc'" href="login.html#/ScanWx" style="text-decoration: none">
            <i class="iconfont" style="color:green;font-size:22px;">&#xe73b;</i>
            </a>
          </div>
        </div>
      </div>
    </el-card>
  </div>
</template>

<script>
  export default {
    name: 'Login',
    data () {
      return {
        user: {
          userName: '',
          passwd: '',
          captcha: '',
        },
        imgUrl: 'imgCode',
      }
    },

    mounted(){

    },
    methods: {
        register(){
            this.$router.push({path: '/Register', query: {random: new Date().getTime()}});
        },
      refresh(){
        this.imgUrl = 'imgCode?dt=' + new Date();
      },
      login(){
        let _reqObj = {url: 'login', method: 'post', data: this.user}
        this.SERVICE2(_reqObj).then(res => {
          //console.log(JSON.stringify(res.data.res))
          window.location.href = "design.html#/SurveyList";
        }).catch((res) => {

        })
      }
    }
  }
</script>

<style type="less">

  .thumbnail {
    border: none;
  }

</style>



