<template>
  <div>
    <div id="container">
      <div style="text-align: center">
        <div>
          <span style="color:#fff;font-size:20px">微信登录小白问卷</span>
        </div>
        <div style="margin-top:15px">
          <img :src="qcodeImg">
        </div>
      </div>
    </div>
  </div>
</template>

<script>
  export default {
    name: 'ScanWx',
    data () {
      return {
        qcodeImg:'',
        priId:'',
        interval: null,
      }
    },
    created(){
      let _reqObj = {url: 'getPriId', params:{}}
      this.SERVICE2(_reqObj).then(res => {
        this.priId = res.data.res;
        this.qcodeImg = this.baseURL+"getScanLoginQrcode?priId="+this.priId;
      }).catch((res) => {

      })
    },
    mounted(){
      var iWidth = 350; //弹出窗口的宽度;
      var iHeight = 360; //弹出窗口的高度;
      var iTop = (document.documentElement.clientHeight - iHeight) / 2; //获得窗口的垂直位置;
      var iLeft = (document.documentElement.clientWidth - iWidth) / 2; //获得窗口的水平位置;
      $("#container").height(iHeight)
        .width(iWidth)
        .css('position', 'absolute')
        .css("top", iTop)
        .css("left", iLeft)

      this.intervalTime()
    },
    destroyed() {
        if(this.interval){
          clearInterval(this.interval );//停止
        }
    },
    methods: {
      intervalTime(){
        this.interval = window.setInterval(this.checkWxScanState, 2000);
      },
      checkWxScanState(){
        let _reqObj = {url: 'wxScanState', params:{priId:this.priId}}
        this.SERVICE2(_reqObj).then(res => {
          if(res.data.res){
            clearInterval(this.interval );//停止
            window.location.href = "design.html#/SurveyList";
          }
        })
      },
    }
  }
</script>

<style type="less">


</style>



